import { Modal, Button, Container, MediaBox } from 'react-materialize';
import React, { Component } from 'react';
import data from "../data/gallery";

class Example2 extends Component {
    render() {
        return (
            <span>
                {
                    data.Gallery.map((item, i) => {
                        return (
                            <span key={i}>
                                <a href={item.url}>
                                    {/* <img src={item.logo} alt={item.id} /> */}
                                    <Modal
                                        actions={[
                                            <Button flat modal="close" node="button" waves="green">Close</Button>
                                        ]}
                                        bottomSheet={false}
                                        fixedFooter={false}
                                        header="Modal Header"
                                        id={item.id}
                                        open={false}
                                        options={{
                                            dismissible: true,
                                            endingTop: '10%',
                                            inDuration: 250,
                                            onCloseEnd: null,
                                            onCloseStart: null,
                                            onOpenEnd: null,
                                            onOpenStart: null,
                                            outDuration: 250,
                                            preventScrolling: true,
                                            startingTop: '4%'
                                        }}
                                        trigger={<img src={item.thumbnail} />}
                                    >
                                        <MediaBox
                                            id="MediaBox_7"
                                            options={{
                                                inDuration: 275,
                                                onCloseEnd: null,
                                                onCloseStart: null,
                                                onOpenEnd: null,
                                                onOpenStart: null,
                                                outDuration: 200
                                            }}
                                        >

                                            <img
                                                className="responsive-img"
                                                alt={item.id}
                                                src={item.img}
                                            /></MediaBox>
                                    </Modal>
                                </a>
                                <div>
                                    <div>
                                        <a href={item.url}>{item.id}</a>
                                    </div>
                                    {item.roles.map(function (role, i) {
                                        return <div key={i}>
                                            <h5>{role.title}</h5>
                                            <span>{role.startDate}</span>
                                            <span>{role.location}</span>
                                            <p>{role.description}</p>
                                        </div>
                                    })}
                                </div>
                            </span>
                        );
                    })
                }
            </span>
        );
    }
}
class Gallery extends Component {
    render() {
        return (
            <Container >
                <Example2 />
            </Container>
        );
    }
}
export default Gallery;
