import React from 'react';
import { Container, Row, Col } from 'react-materialize';
import Nav from '../elements/Nav'
import Title, { flushTitle } from 'reactjs-title'

function Div(props) {
  return <h5>Please sign up.</h5>;
}


export default class Main extends React.Component {
  render() {
    if (this.props.nav) {
      return <span>
        <Title render={this.props.name}/>
        <Nav />
        <Container>
          <Row>
            <Col>
              <h5>{this.props.name}</h5>
            </Col>
          </Row>
          <Row>
          <Col>
            <h5>{this.props.gallery}</h5>
            </Col>
          </Row>
        </Container>
      </span>;
    }
    
    return <span>
      <Title render={this.props.name}/>
      <Container>
          <Row>
            <Col>
              <h5>{this.props.name}</h5>
            </Col>
          </Row>
          <Row>
          <Col>
            <h5>{this.props.gallery}</h5>
            </Col>
          </Row>
        </Container>
      <Div />
    </span>;
  }
}

